interface SpringError {
	detail?: string;
	message?: string;
}

interface Comment {
	id: number;
	content: string;
	author: {
		username: string;
	};
	createdAt: string;
	updatedAt: string;
}

interface CommentRequest {
	content: string;
}

function parseSpringError(res: Response, errorData: SpringError): string {
    return errorData?.detail || errorData?.message || res.statusText || 'Unknown error';
}

export async function createComment(id: string | number, token: string, content: string): Promise<Comment> {
    const res = await fetch(`/api/articles/${id}/comments`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ content })
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function fetchCommentsByArticleId(id: string | number): Promise<Comment[]> {
    const res = await fetch(`/api/articles/${id}/comments`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function editComment(articleId: string | number, commentId: string | number, content: string, token: string): Promise<Comment> {
    const res = await fetch(`/api/articles/${articleId}/comments/${commentId}`, {
        method: 'PUT',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({content})
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function deleteComment(articleId: string | number, commentId: string | number, token: string): Promise<void> {
    const res = await fetch(`/api/articles/${articleId}/comments/${commentId}`, {
        method: 'DELETE',
        headers: {
            'Authorization': `Bearer ${token}`
        }
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
} 