import React, { useContext, useState } from 'react';
import { ArticleControlsContext } from '../layouts/Layout';
import '../format/TopBar.css';

interface SortField {
  label: string;
  value: string;
}

interface SortCriteria {
  field: string;
  direction: 'asc' | 'desc';
}

const SORT_FIELDS: SortField[] = [
  { label: 'Date', value: 'createdDate' },
  { label: 'Title', value: 'title' },
  { label: 'Author', value: 'author' },
];

const PAGE_SIZES: number[] = [5, 10, 20, 50];

const TopBar: React.FC = () => {
  const context = useContext(ArticleControlsContext);
  
  if (!context) {
    throw new Error('TopBar must be used within ArticleControlsContext');
  }
  
  const {
    filtersInput, setFiltersInput, filters, setFilters,
    sortCriteria, setSortCriteria, pageSize, setPageSize, pageIndex, setPageIndex, sizeInput, setSizeInput
  } = context;

  const [localSort, setLocalSort] = useState<SortCriteria[]>(sortCriteria);
  const [expanded, setExpanded] = useState<boolean>(false);

  // Handle sort field toggle
  const handleSortToggle = (field: string): void => {
    const exists = localSort.find(sc => sc.field === field);
    let updated: SortCriteria[];
    if (exists) {
      updated = localSort.filter(sc => sc.field !== field);
    } else {
      updated = [...localSort, { field, direction: 'asc' }];
    }
    setLocalSort(updated);
    setSortCriteria(updated);
  };

  // Handle sort direction toggle
  const handleSortDirection = (field: string): void => {
    const updated = localSort.map(sc =>
      sc.field === field ? { ...sc, direction: sc.direction === 'asc' ? 'desc' as const : 'asc' as const } : sc
    );
    setLocalSort(updated);
    setSortCriteria(updated);
  };

  // Filter pills
  const handleFilterChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    setFiltersInput({ ...filtersInput, [e.target.name]: e.target.value });
  };
  
  const applyFilters = (): void => setFilters(filtersInput);
  
  const clearFilters = (): void => {
    setFiltersInput({ title: '', author: '' });
    setFilters({ title: '', author: '' });
  };

  const applySizeChange = (): void => {
    setPageSize(sizeInput);
    setPageIndex(0);
  };

  const clearPageSize = (): void => {
    setSizeInput(10);
    setPageSize(10);
    setPageIndex(0);
  };

  return (
    <div className="topbar">
      <button 
        className="topbar-toggle" 
        onClick={() => setExpanded(!expanded)}
        aria-expanded={expanded}
      >
        {expanded ? 'Hide Filters' : 'Show Filters'} ▼
      </button>
      
      {expanded && (
        <div className="topbar-content">
          {/* Filters Section */}
          <div className="topbar-section">
            <h3>Filters</h3>
            <div className="filter-row">
              <input
                type="text"
                name="title"
                value={filtersInput.title}
                onChange={handleFilterChange}
                placeholder="Filter by title..."
                className="filter-input"
              />
              <input
                type="text"
                name="author"
                value={filtersInput.author}
                onChange={handleFilterChange}
                placeholder="Filter by author..."
                className="filter-input"
              />
            </div>
            <div className="filter-actions">
              <button onClick={applyFilters} className="btn btn-primary">Apply</button>
              <button onClick={clearFilters} className="btn btn-secondary">Clear</button>
            </div>
          </div>

          {/* Sort Section */}
          <div className="topbar-section">
            <h3>Sort</h3>
            <div className="sort-fields">
              {SORT_FIELDS.map(field => {
                const activeCriteria = localSort.find(sc => sc.field === field.value);
                return (
                  <div key={field.value} className="sort-field">
                    <label>
                      <input
                        type="checkbox"
                        checked={!!activeCriteria}
                        onChange={() => handleSortToggle(field.value)}
                      />
                      {field.label}
                    </label>
                    {activeCriteria && (
                      <button
                        onClick={() => handleSortDirection(field.value)}
                        className="sort-direction-btn"
                      >
                        {activeCriteria.direction === 'asc' ? '↑' : '↓'}
                      </button>
                    )}
                  </div>
                );
              })}
            </div>
          </div>

          {/* Page Size Section */}
          <div className="topbar-section">
            <h3>Page Size</h3>
            <div className="page-size-row">
              <select
                value={sizeInput}
                onChange={(e) => setSizeInput(Number(e.target.value))}
                className="page-size-select"
              >
                {PAGE_SIZES.map(size => (
                  <option key={size} value={size}>{size}</option>
                ))}
              </select>
              <div className="page-size-actions">
                <button onClick={applySizeChange} className="btn btn-primary">Apply</button>
                <button onClick={clearPageSize} className="btn btn-secondary">Reset</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default TopBar; 