import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router';
import { createArticle, updateArticle, fetchArticleById } from '../api/ArticlesApi';
import '../format/ArticleForm.css';
import { useAuth } from '../api/AuthContext';

interface ArticleFormProps {
	isEdit?: boolean;
}

const ArticleForm: React.FC<ArticleFormProps> = ({ isEdit = false }) => {
	const { id } = useParams<{ id: string }>();
	const [title, setTitle] = useState<string>('');
	const [content, setContent] = useState<string>('');
	const navigate = useNavigate();

	const {token, currentUser} = useAuth();
	console.log(token);
	console.log(currentUser);

	useEffect(() => {
		if (isEdit && id) {
			fetchArticleById(id).then(article => {
				setTitle(article.title);
				setContent(article.content);
			});
		}
	}, [isEdit, id]);

	const handleSubmit = async (e: React.FormEvent<HTMLFormElement>): Promise<void> => {
		e.preventDefault();
		const articleData = { title, content };
		
		try {
			if (isEdit && id && token) {
				await updateArticle(id, articleData, token);
			} else if (token) {
				await createArticle(articleData, token);
			}
			navigate('/articles');
		} catch (error: any) {
			console.error('Error saving article:', error);
			// Handle error appropriately
		}
	};

	return (
		<div className="article-form-container">
			<h2>{isEdit ? 'Edit Article' : 'Create New Article'}</h2>
			<form onSubmit={handleSubmit} className="article-form">
				<div className="form-group">
					<label htmlFor="title">Title:</label>
					<input
						type="text"
						id="title"
						value={title}
						onChange={(e) => setTitle(e.target.value)}
						required
					/>
				</div>
				<div className="form-group">
					<label htmlFor="content">Content:</label>
					<textarea
						id="content"
						value={content}
						onChange={(e) => setContent(e.target.value)}
						required
						rows={10}
					/>
				</div>
				<div className="form-actions">
					<button type="submit" className="btn btn-primary">
						{isEdit ? 'Update Article' : 'Create Article'}
					</button>
					<button 
						type="button" 
						className="btn btn-secondary" 
						onClick={() => navigate('/articles')}
					>
						Cancel
					</button>
				</div>
			</form>
		</div>
	);
};

export default ArticleForm; 