interface SpringError {
	detail?: string;
	message?: string;
}

interface User {
	id: number;
	username: string;
	firstName: string;
	lastName: string;
	email: string;
	role: string;
	createdAt: string;
	updatedAt: string;
}

interface Role {
	role: string;
}

const authHeader = (): Record<string, string> => ({
    'Authorization': `Bearer ${JSON.parse(localStorage.getItem('token') || 'null')}`,
    'Content-Type': 'application/json'
});

function parseSpringError(res: Response, errorData: SpringError): string {
    // Spring Boot 3+ ProblemDetail: 'detail', legacy: 'message', fallback: status text
    return errorData?.detail || errorData?.message || res.statusText || 'Unknown error';
}

export async function fetchUserById(id: string | number): Promise<User> {
    const res = await fetch(`/api/users/${id}`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function fetchCurrentUser(username: string): Promise<User> {
    const res = await fetch(`/api/users/${username}`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function fetchUsers(): Promise<User[]> {
    const res = await fetch(`/api/users`, {
        headers: authHeader(),
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function updateUserRole(id: string | number, newRole: Role): Promise<User> {
    const res = await fetch(`/api/users/${id}/role`, {
        method: 'PUT',
        headers: authHeader(),
        body: JSON.stringify(newRole)
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function deleteUser(id: string | number): Promise<void> {
    const res = await fetch(`/api/users/${id}`, {
        method: 'DELETE',
        headers: authHeader()
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
} 