import React, { useEffect, useState, useContext, useRef } from 'react';
import { NavLink, useNavigate } from 'react-router';
import { fetchAllArticles } from '../api/ArticlesApi';
import { ChronoUnit } from '@js-joda/core';
import { ArticleControlsContext } from '../layouts/Layout';
import { Article, ArticlesResponse } from '../types/common';

import '../format/Articles.css';

const Articles: React.FC = () => {
	const context = useContext(ArticleControlsContext);
	
	if (!context) {
		throw new Error('Articles must be used within ArticleControlsContext');
	}
	
	const {
		filtersInput, setFiltersInput, filters, setFilters,
		sortCriteria, setSortCriteria, pageSize, setPageSize, pageIndex, setPageIndex, sizeInput, setSizeInput
	} = context;
	
	const [articles, setArticles] = useState<ArticlesResponse | null>(null);
	const navigate = useNavigate();
	const [showBottomBar, setShowBottomBar] = useState<boolean>(false);
	const lastArticleRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        fetchAllArticles({
            filters,
            sortCriteria,
            size: pageSize,
            from: pageIndex
        }).then(setArticles)
        .catch((err: any) => {
            if (err.message && err.message.toLowerCase().includes('forbidden')) {
                navigate('/forbidden');
            } else if (err.message && err.message.toLowerCase().includes('not found')) {
                navigate('/notfound');
            } else {
                navigate('/error');
            }
        });
    }, [filters, sortCriteria, pageSize, pageIndex, navigate]);

	useEffect(() => {
		const observer = new window.IntersectionObserver(
			(entries) => {
				entries.forEach(entry => {
					if (entry.isIntersecting) {
						setShowBottomBar(true);
					} else {
						setShowBottomBar(false);
					}
				});
			},
			{ threshold: 0.1, rootMargin: '0px 0px -50px 0px' }
		);

		if (lastArticleRef.current) {
			observer.observe(lastArticleRef.current);
		}

		return () => {
			if (lastArticleRef.current) {
				observer.unobserve(lastArticleRef.current);
			}
		};
	}, [articles]);

	const formatDate = (isoString: string): string => {
		try {
			const date = new Date(isoString);
			return date.toLocaleDateString('en-US', {
				year: 'numeric',
				month: 'short',
				day: 'numeric'
			});
		} catch (error) {
			return 'Unknown';
		}
	};

	const formatContent = (content: string): string => {
		return content.length > 200 ? content.substring(0, 200) + '...' : content;
	};

	const handlePrevious = (): void => {
		if (pageIndex > 0) {
			setPageIndex(pageIndex - 1);
		}
	};

	const handleNext = (): void => {
		if (articles && pageIndex < articles.totalPages - 1) {
			setPageIndex(pageIndex + 1);
		}
	};

	if (!articles) {
		return <div className="articles-loading">Loading articles...</div>;
	}

	return (
		<div className="articles-container">
			<div className="articles-header">
				<h1>Articles ({articles.totalElements})</h1>
			</div>

			<div className="articles-list">
				{articles.content.length === 0 ? (
					<div className="no-articles">
						<p>No articles found.</p>
					</div>
				) : (
					articles.content.map((article, index) => (
						<div 
							key={article.id} 
							className="article-card"
							ref={index === articles.content.length - 1 ? lastArticleRef : null}
						>
							<div className="article-header">
								<h2 className="article-title">
									<NavLink to={`/articles/${article.id}`} className="article-link">
										{article.title}
									</NavLink>
								</h2>
								<div className="article-meta">
									<span className="article-author">
										by <NavLink to={`/users/${article.author.username}`} className="author-link">
											{article.author.username}
										</NavLink>
									</span>
									<span className="article-date">{formatDate(article.createdAt)}</span>
								</div>
							</div>
							<div className="article-content">
								{formatContent(article.content)}
							</div>
						</div>
					))
				)}
			</div>

			{showBottomBar && (
				<div className="articles-pagination">
					<button 
						onClick={handlePrevious} 
						disabled={pageIndex === 0}
						className="pagination-btn"
					>
						Previous
					</button>
					<span className="pagination-info">
						Page {pageIndex + 1} of {articles.totalPages}
					</span>
					<button 
						onClick={handleNext} 
						disabled={pageIndex >= articles.totalPages - 1}
						className="pagination-btn"
					>
						Next
					</button>
				</div>
			)}
		</div>
	);
};

export default Articles; 