import { jwtDecode } from 'jwt-decode';

interface RegisterUserRequest {
	lastName: string;
	firstName: string;
	username: string;
	password: string;
	email: string;
}

interface LoginUserRequest {
	username: string;
	password: string;
}

interface LoginResponse {
	token: string;
	username: string;
}

interface DecodedToken {
	sub: string;
	authorities: Array<{ authority: string }>;
}

interface CurrentUser {
	username: string;
	authorities: string[];
}

export async function registerUser({ lastName, firstName, username, password, email }: RegisterUserRequest): Promise<any> {
	const res = await fetch('/api/users/register', {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json',
		},
		body: JSON.stringify({ lastName, firstName, username, password, email }),
	});

	if (!res.ok) {
		const errorData = await res.json().catch(() => ({}));
		throw new Error(errorData.message || 'Register failed');
	}

	return res.json();
}

export async function loginUser({ username, password }: LoginUserRequest): Promise<LoginResponse> {
	const res = await fetch('/api/users/login', {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json',
		},
		body: JSON.stringify({ username, password }),
	});

	if (!res.ok) {
		const errorData = await res.json().catch(() => ({}));
		throw new Error(errorData.message || 'Login failed');
	}

	return res.json();
}

export function getCurrentUser(): CurrentUser | null {
	const token = localStorage.getItem('token');
	if (!token) return null;

	try {
		const decoded = jwtDecode<DecodedToken>(token);
		return {
			username: decoded.sub,
			authorities: decoded.authorities.flatMap(authority => authority.authority),
		};
	} catch (error) {
		console.error('Error decoding token:', error);
		return null;
	}
}

export function hasRole(role: string): boolean {
	const user = getCurrentUser();
	if (!user || !user.username || !user.authorities) return false;
	return user.authorities.includes("ROLE_" + role);
}

export function hasUser(username: string): boolean {
	const user = getCurrentUser();
	if (!user || !user.username || !user.authorities) return false;
	console.log("TEST permission" + user + username);
	return user.username === username;
} 