interface SpringError {
	detail?: string;
	message?: string;
}

interface ArticleFilters {
	title?: string;
	author?: string;
	content?: string;
	[key: string]: string | undefined;
}

interface SortCriteria {
	field: string;
	direction: 'asc' | 'desc';
}

interface FetchArticlesParams {
	filters: ArticleFilters;
	sortCriteria: SortCriteria[];
	size?: number;
	from?: number;
}

interface Author {
	username: string;
}

interface Article {
	id: number;
	title: string;
	content: string;
	author: Author;
	createdAt: string;
	updatedAt: string;
}

interface ArticleRequest {
	title: string;
	content: string;
}

interface ArticlesResponse {
	content: Article[];
	totalElements: number;
	totalPages: number;
	size: number;
	number: number;
}

function parseSpringError(res: Response, errorData: SpringError): string {
    return errorData?.detail || errorData?.message || res.statusText || 'Unknown error';
}

export async function fetchAllArticles({ filters, sortCriteria, size = 10, from = 0 }: FetchArticlesParams): Promise<ArticlesResponse> {
    const params = new URLSearchParams();
    params.set('size', size.toString());
    params.set('from', from.toString());
    Object.entries(filters).forEach(([key, value]) => {
        if (value) params.append(key, value);
    });
    if (sortCriteria.length > 0) {
        const sortParam = sortCriteria.map(sc => `${sc.field} ${sc.direction}`).join(',');
        params.set('sort', sortParam);
    }
    const res = await fetch(`/api/articles?${params.toString()}`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function fetchArticleById(id: string | number): Promise<Article> {
    const res = await fetch(`/api/articles/${id}`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function fetchArticlesByTitle(title: string): Promise<ArticlesResponse> {
    const res = await fetch(`/api/articles?title=${title}`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function fetchArticlesByAuthor(author: Author): Promise<ArticlesResponse> {
    const res = await fetch(`/api/articles?author=${author.username}`);
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function createArticle(article: ArticleRequest, token: string): Promise<Article> {
    const res = await fetch('/api/articles', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(article),
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function updateArticle(id: string | number, article: ArticleRequest, token: string): Promise<Article> {
    const res = await fetch(`/api/articles/${id}`, {
        method: 'PUT',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(article)
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
    return res.json();
}

export async function deleteArticle(id: string | number, token: string): Promise<void> {
    const res = await fetch(`/api/articles/${id}`, {
        method: 'DELETE',
        headers: {
            'Authorization': `Bearer ${token}`
        },
    });
    if (!res.ok) {
        let errorData: SpringError = {};
        try { errorData = await res.json(); } catch {}
        throw new Error(parseSpringError(res, errorData));
    }
} 